.header {
  width: 100%;
  position: fixed;
  z-index: 1000;
  transition: 300ms;
  display: block;
  left: 50%;
  transform: translate(-50%, 0%);
  border-radius: 20px;
  display: flex;
}

.header .headerLeft {
  align-items: center;
  display: flex;
  align-self: center;
}

.header .headerLeft img {
  width: 4em;
  display: block;
  margin-left: 10px;
}

.header .headerRight {
  align-items: center;
  display: flex;
  align-self: center;
}

.header .headerRight div {
  text-decoration: none;
}

.header .headerRight div .btnContact {
  width: 67.5px;
  display: block;
  margin-right: 10px;
  height: 65px;
  font-size: 1.8em;
  font-weight: 600;
  border-radius: 20px;
  font-family: 'Montserrat', sans-serif;
  transition: 300ms;
  padding-top: 7.5px;
}

.header .headerRight div .btnContact:hover {
  cursor: pointer;
}

.header .headerRight div .popupIn {
  display: flex;
  flex-direction: column;
}

.header nav {
  width: 1100px;
  margin-left: auto;
  margin-right: auto;
}

.header nav ul {
  display: flex;
  justify-content: space-around;
}

.header nav ul li {
  padding: 15px;
  list-style-type: none;
}

.header nav ul li a {
  text-decoration: none;
  font-family: 'Lato', sans-serif;
  font-weight: 900;
  font-size: 1.1em;
  transition: 300ms;
}

.list {
  width: 250px;
  padding-top: 50px;
  height: 100vh;
}

.list .listToggle {
  text-align: center;
  margin-bottom: 50px;
}

.list .listToggle a {
  font-size: 4em;
  transition: 300ms;
}

.list .listToggle a:hover {
  opacity: 80%;
  font-size: 4.5em;
}

.header.active {
  width: 98.5%;
  margin-top: 15px;
}

.headerMobile {
  display: none;
}

@media all and (max-width: 800px) {
