.hero {
  position: relative;
  height: 100vh;
  overflow: hidden;
  font-family: 'Lato', sans-serif;
}

.hero .hero__title {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  font-size: 50px;
  z-index: 1;
  font-weight: 300;
  text-align: center;
}

.hero .hero__title span {
  font-weight: 600;
}

.hero .hero__arrow {
  position: absolute;
  top: 90%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 1;
}

.hero .hero__arrow a img {
  width: 80px;
  transition: 300ms;
}

.hero .hero__arrow a:hover img {
  width: 100px;
}

.hero .cube {
  position: absolute;
  top: 80vh;
  left: 45vw;
  width: 10px;
  height: 10px;
  border-radius: 2px;
  transform-origin: top left;
  transform: scale(0) rotate(0deg) translate(-50%, -50%);
  -webkit-animation: cube 20s ease-in forwards infinite;
          animation: cube 20s ease-in forwards infinite;
}

.hero .cube:nth-child(2) {
  -webkit-animation-delay: 2s;
          animation-delay: 2s;
  left: 25vw;
  top: 40vh;
}

.hero .cube:nth-child(3) {
  -webkit-animation-delay: 4s;
          animation-delay: 4s;
  left: 75vw;
  top: 50vh;
}

.hero .cube:nth-child(4) {
  -webkit-animation-delay: 6s;
          animation-delay: 6s;
  left: 90vw;
  top: 10vh;
}

.hero .cube:nth-child(5) {
  -webkit-animation-delay: 8s;
